        </div>
        <div
          @click="goContact()"
          v-if="status != 'contact'"
          class="nav__menu--button"
        >
          <h2>Contact</h2>
        </div>
      </div>
    </div>
  </nav>
  <!--NAVIGATION-->

  <!--MAIN HOME-->
  <main v-if="status == 'home'" class="main">
