              </div>
              <div class="back__box__techno__prog">
                <div class="back__box__techno__prog--size"></div>
              </div>
              <div class="colorP">Git</div>
            </div>

            <div class="back__box__techno">
              <div class="back__box__techno__icon">
                <i
                  class="fa-brands fa-sass back__box__techno__icon--size colorPink"
