                  class="fa-brands fa-npm back__box__techno__icon--size colorRed"
                ></i>
              </div>
              <div class="back__box__techno__prog">
                <div class="back__box__techno__prog--sizeJs"></div>
              </div>
              <div class="colorP">Npm</div>
            </div>

            <div class="back__box__techno">
              <div class="back__box__techno__icon">
                <i
                  class="fa-solid fa-database back__box__techno__icon--size colorBlue"
                ></i>
              </div>
              <div class="back__box__techno__prog">
                <div class="back__box__techno__prog--sizeVue"></div>
              </div>
