      </div>
    </div>
  </main>
  <!--MAIN HOME-->

  <!--MAIN PROJET-->

  <main v-if="status == 'projet'" class="mainProjet">
    <div v-if="visuStatus == true" class="visuProjet">
      <div class="visuProjet__card">
        <div @click="projetClose()" class="previousVisu">
          <i class="fa-solid fa-xmark previousVisu--size"></i>
        </div>

        <div class="visuProjet__card__title">
          <h1 class="visuProjet__card__title--size">
            {{ visuProjet[0].title }}
          </h1>
        </div>
        <hr />
        <div class="visuProjet__card__description">
          <p class="visuProjet__card__description--size">
            {{ visuProjet[0].description }}
          </p>
        </div>
        <div
          v-if="this.visuProjet[0].competence"
          class="visuProjet__card__competence"
        >
          <h2 class="visuProjet__card__competence--size">
            Compétences évaluées
          </h2>
