              <a :href="visuProjet[0].git" target="_blank">Cliquez ici</a>
            </p>
          </div>
          <div
            v-if="this.visuProjet[0].visual"
            class="visuProjet__card__link__p"
          >
            <p>
              Voir projet :
              <a :href="visuProjet[0].visual" target="_blank">Cliquez ici</a>
            </p>
