          <p class="visuProjet__card__pdf--size">Projet de formation</p>
        </div>
      </div>
    </div>

    <div class="boxProjet">
      <div class="boxProjetId animOpen">
        <div
          v-for="projet in projets"
          :key="projet"
          class="boxProjet__card"
          @click="projetOpen(projet)"
        >
          <div class="boxProjet__card__img">
            <img
              :src="projet.imgtt"
