          >
        </div>
      </div>
    </div>
  </main>
  <!--MAIN CV-->

  <!--MAIN CONTACT-->
  <main v-if="status == 'contact'" class="main">
    <div class="box3d">
      <div id="boxContact" class="boxContact animOpen">
        <div
          id="mailHover"
          @pointerenter="mailMouse()"
          @pointerleave="mailMouseOut()"
        >
          <a href="mailto:julienferrini1995@gmail.com">
            <i class="fa-solid fa-square-envelope boxContact__icon"></i>
          </a>
        </div>

        <div
          id="phoneHover"
          @pointerenter="phoneMouse()"
          @pointerleave="phoneMouseOut()"
        >
          <a href="tel:+33678089879"
            ><i class="fa-solid fa-square-phone boxContact__icon"></i
          ></a>
        </div>
        <div
          id="linkedinHover"
          @pointerenter="linkedinMouse()"
          @pointerleave="linkedinMouseOut()"
        >
          <a
            href="https://www.linkedin.com/in/julien-ferrini-450654234/"
            target="_blank"
            ><i class="fa-brands fa-linkedin boxContact__icon"></i
          ></a>
        </div>
      </div>
      <p id="pHover" class="alertHoverNone">
        Cliquez pour envoyer un mail à julienferrini1995@gmail.com !
      </p>
      <p id="pHoverPhone" class="alertHoverNone">
        Cliquez pour appeler le 06 78 08 98 79 !
      </p>
      <p id="pHoverLinkedin" class="alertHoverNone">
        Cliquez pour être redirigé vers le LinkedIn de Julien Ferrini !
      </p>
    </div>
  </main>
  <!--MAIN CONTACT-->
</template>

<script>
export default {
  name: "HomeView",
  data: function () {
    return {
      face: "front",
      info: true,
      preview: true,
      navOp: true,
      status: "home",
      visuStatus: false,
      projets: [
        {
          id: 1,
          title: "Réservia",
          imgtt: require("../assets/reservia.png"),
          description:
            "Avant de valider définitivement le design, l’entreprise a décidé de réaliser un prototype. Vous êtes chargé de créer ce prototype en intégrant la maquette en HTML et CSS.",
          competence: [
            { c: "1- Implémenter une interface responsive" },
            { c: "2- Intégrer du contenu conformément à une maquette" },
          ],

          git: "https://github.com/J-F-Dev/JulienFerrini_2_27-05-2021",
          visual: "https://j-f-dev.github.io/JulienFerrini_2_27-05-2021/",
        },
        {
          id: 2,
          title: "Ohmyfood",
          imgtt: require("../assets/ohmyfood.png"),
          description:
            "Vous faites partie des 4 heureux élus qui ont la chance de travailler sur ce beau projet. Anissa est en plein démarchage pour ce projet, auprès des restaurateurs. Elle vous interpelle à la machine à café : Tu connais pas la nouvelle ? Tu te rappelles le responsable commercial de ton resto préféré ? J’ai réussi à le convaincre, avec 3 autres, de déposer leurs cartes en ligne ! Je t’envoie le dossier avec les 4 menus dans l’après-midi. À toi de jouer, maintenant ! Vous décidez alors, avec l’équipe, que le site contiendra 4 menus dans un premier temps. Voici le brief que vous établissez avec le CTO, Paul. De retour à votre bureau, vous trouvez un mail de Fanny qui vous envoie les maquettes du site. Pour ce projet, vous versionnez tout votre code sur Github avec des commits réguliers pour suivre son avancement et publier le site en ligne plus facilement. Vous avez désormais tous les éléments pour construire ce site mobile. Vous vous lancez dans cette nouvelle aventure !",
          competence: [
            { c: "1- Assurer la cohérence graphique d'un site web" },
            {
              c: "2- Utiliser un système de gestion de versions pour le suivi du projet et son hébergement",
            },
            { c: "3- Mettre en œuvre des effets CSS graphiques avancés" },
            { c: "4- Mettre en place son environnement Front-End" },
            {
              c: "5- Mettre en place une structure de navigation pour un site web",
            },
          ],
          git: "https://github.com/J-F-Dev/JulienFerrini_3_27-05-2021",
          visual: "https://j-f-dev.github.io/JulienFerrini_3_27-05-2021/",
        },
        {
          id: 3,
          title: "La chouette agence",
          imgtt: require("../assets/chouette.png"),
          description:
            "J’aimerais que tu indiques les parties du site qui ne sont pas optimisées pour le SEO et/ou qui ne sont pas à jour en termes d’accessibilité, et que tu justifies tes choix. Pense à ajouter la bonne pratique à mettre en place pour chaque partie du site qui n’est pas optimisée, en citant tes sources. Il faudra ensuite optimiser notre site en appliquant tes 10 recommandations à son contenu et à son code source. Tu devras pour cela fournir le code source complet de la version améliorée du site. Le site doit passer le W3C pour HTML et CSS. La vitesse de chargement des pages du site a un impact sur notre classement dans les résultats de recherche. Dans tes différentes recommandations, j’aimerais que tu prennes des mesures pour accélérer la vitesse de chargement, et que tu me démontres que cela fonctionne, grâce à des captures d’écran comparatives entre la version du site actuelle et celle que tu auras optimisée, fourni dans un rapport d’optimisation. Ce rapport devra inclure les comparaisons pour les 10 recommandations.",
          competence: [
            { c: "1- Écrire un code HTML et CSS maintenable" },
            {
              c: "2- Assurer l'accessibilité d'un site web",
            },
            { c: "3- Optimiser le référencement d'un site web" },
            {
              c: "4- Réaliser une recherche des bonnes pratiques en développement web",
            },
            {
              c: "5- Optimiser la taille et la vitesse d’un site web",
            },
          ],
          git: "https://github.com/J-F-Dev/JulienFerrini_4_27-05-2021-",
          visual: "https://j-f-dev.github.io/JulienFerrini_4_27-05-2021-/",
        },
        {
          id: 4,
          title: "Kanap",
          imgtt: require("../assets/projet5.png"),
          description:
            "4 pages ont été mises en place : page d’accueil, page Produit, page Panier et la page Confirmation. Sur l’ensemble des pages, toutes les parties statiques sont en place, elles sont donc prêtes à recevoir le contenu dynamique. Aussi, sur chaque page, un exemple de la partie dynamique est systématiquement donné ; de cette façon, tu n’as pas à t’occuper de la mise en place de la structure HTML ni du style CSS, tout est déjà fait. Tu n’as plus qu’à t’occuper d’intégrer ces éléments dynamiquement grâce à JS et l’API. Enfin, dans le code HTML j’ai intégré des “id” dans différentes balises, cela devrait t’aider à intégrer les éléments dynamiques. Avec tout ça, normalement tu n’auras pas besoin de toucher au code HTML/CSS.",
          competence: [
            { c: "1- Interagir avec un web service avec JavaScript" },
            {
              c: "2- Gérer des événements JavaScript",
            },
            { c: "3- Valider des données issues de sources externes" },
            {
              c: "4- Créer un plan de test pour une application",
            },
          ],
          git: "https://github.com/J-F-Dev/JulienFerrini_5_27-05-2021",
        },
        {
          id: 5,
          title: "Piiquante",
          imgtt: require("../assets/projet6.png"),
          description:
            "Si la responsable produit de Piiquante souhaite à terme transformer l'application d'évaluation en une boutique en ligne, elle souhaite que la première version soit une « galerie de sauces » permettant aux utilisateurs de télécharger leurs sauces piquantes préférées et de liker ou disliker les sauces que d'autres partagent. Le front-end de l'application a été développé à l'aide d'Angular et a été précompilé après des tests internes, mais Piiquante a besoin d'un développeur back-end pour construire l'API. Merci de faire particulièrement attention aux exigences en matière de sécurité. Nous avons récemment été victimes d'attaques sur notre site web et nous voulons être sûrs que l'API de cette application est construite selon des pratiques de code sécurisées. Tous les mots de passe des utilisateurs recueillis par l'application doivent être protégés !",
          competence: [
            {
              c: "1- Implémenter un modèle logique de données conformément à la réglementation",
            },
            {
              c: "2- Stocker des données de manière sécurisée",
            },
            {
              c: "3- Mettre en œuvre des opérations CRUD de manière sécurisée",
            },
          ],
          git: "https://github.com/J-F-Dev/JulienFerrini_6_27-05-2021",
        },
        {
          id: 6,
          title: "Groupomania",
          imgtt: require("../assets/projet7.png"),
          description:
            "Le projet consiste à construire un réseau social interne pour les employés de Groupomania. Le but de cet outil est de faciliter les interactions entre collègues. Le département RH de Groupomania a laissé libre cours à son imagination pour les fonctionnalités du réseau et a imaginé plusieurs briques pour favoriser les échanges entre collègues. Après plusieurs réunions avec Groupomania, il semble que les paramètres du projet changent régulièrement, je pense qu’une organisation “agile” serait plus adaptée pour s’adapter aux besoins du client au fur et à mesure des commentaires et affiner l’application au fil de l’eau. La seule contrainte est que le client utilise une base de données relationnelles qui se manipule avec le langage SQL pour le stockage de données. Il faudra donc en tenir compte lorsque tu construiras ton application. Tu devras t’assurer que l’utilisateur puisse requêter les données requises depuis SQL et puisse soumettre ces changements à la base de données SQL. Les données de connexion doivent également être sécurisées. Deux dernières choses, tu devras faire en sorte que la web app puisse se connecter et se déconnecter à l’application et que la session de l’utilisateur persiste pendant qu’il est connecté. Pour ce projet, il faut que tu utilises un framework Front-End. Tu peux utiliser celui que tu préfères (React, Vue, Angular, Ember, Meteor, Aurelia...). Par contre, le projet doit être codé en Javascript, donc n'utilise pas le framework Symfony par exemple. Enfin, tes pages devront respecter les standards WCAG.",
          competence: [
            { c: "1- Authentifier un utilisateur et maintenir sa session" },
            {
              c: "2- Personnaliser le contenu envoyé à un client web",
            },
            { c: "3- Gérer un stockage de données à l'aide de SQL" },
            {
              c: "4- Implémenter un stockage de données sécurisé en utilisant SQL",
            },
          ],
          git: "https://github.com/J-F-Dev/P7_Ferrini_Julien",
        },
      ],
      visuProjet: [],
    };
  },
  mounted: function () {
    setTimeout(this.removeAnim, 400);
  },

  methods: {
    timeFront: function () {
      this.face = "back";
    },
    timeBack: function () {
      this.face = "front";
    },
    timeInfo: function () {
      let info = document.getElementById("info");
      let prev = document.getElementById("prev");
      info.classList.add("cacheFront");
      prev.classList.remove("cacheBack");
    },
    timePrev: function () {
      let info = document.getElementById("info");
      let prev = document.getElementById("prev");
      info.classList.remove("cacheFront");
      prev.classList.add("cacheBack");
    },
    timeZindex: function () {
      let zIndex = document.getElementById("zIndex");
      zIndex.classList.remove("zIndex--2");
    },
    timeContact: function () {
      this.status = "contact";
    },
    timeHome: function () {
      this.status = "home";
    },
    timeProjet: function () {
      this.status = "projet";
    },
    timeCv: function () {
      this.status = "cv";
    },
    removeAnim: function () {
      let card = document.getElementById("card");

      card.classList.remove("animOpen");
    },
    removeAnimContact: function () {
      let boxContact = document.getElementById("boxContact");
      boxContact.classList.remove("animClose");
    },
    removeAnimProjet: function () {
      let boxProjetId = document.querySelector(".boxProjetId");
      boxProjetId.classList.remove("animClose");
    },
    removeAnimCv: function () {
      let boxCv = document.getElementById("boxCv");
      boxCv.classList.remove("animClose");
    },

    frontBack: function () {
      let card = document.getElementById("card");
      let front = document.getElementById("front");
      let back = document.getElementById("back");

      card.classList.add("flipCard");
      front.classList.add("flipFront");
      back.classList.add("flipBack");

      setTimeout(front.classList.add("cacheFront"), 1000);
      setTimeout(this.timeFront, 1100);
      setTimeout(this.timeInfo, 1000);
    },
    backFront: function () {
      let card = document.getElementById("card");
      let front = document.getElementById("front");
      let back = document.getElementById("back");

      card.classList.remove("flipCard");
      front.classList.remove("flipFront");
      back.classList.remove("flipBack");

      setTimeout(front.classList.remove("cacheFront"), 200);
      setTimeout(this.timeBack, 1000);
      setTimeout(this.timePrev, 1000);
    },
    menuOpen: function () {
      let menuOp = document.getElementById("menuOp");
      let navIcone = document.getElementById("navIcone");
      let zIndex = document.getElementById("zIndex");
      if (this.navOp == true) {
        zIndex.classList.add("zIndex--2");
        menuOp.classList.add("menuOp");
        navIcone.classList.add("navIcone");
        menuOp.classList.remove("menuClose");
        this.navOp = false;
      } else {
        menuOp.classList.remove("menuOp");
        navIcone.classList.remove("navIcone");
        menuOp.classList.add("menuClose");
        this.navOp = true;
        setTimeout(this.timeZindex, 1000);
      }
    },
    cardMouseEnter: function (event) {
      if (window.matchMedia("(min-width: 600px)").matches) {
        const tiltEffectSettings = {
          max: 25, // max tilt rotation (degrees (deg))
          perspective: 500, // transform perspective, the lower the more extreme the tilt gets (pixels (px))
          scale: 1.1, // transform scale - 2 = 200%, 1.5 = 150%, etc..
          speed: 500, // speed (transition-duration) of the enter/exit transition (milliseconds (ms))
          easing: "cubic-bezier(.03,.98,.52,.99)", // easing (transition-timing-function) of the enter/exit transition
        };
        const card = event.currentTarget;
        clearTimeout(card.transitionTimeoutId);
        card.style.transition = `transform ${tiltEffectSettings.speed}ms ${tiltEffectSettings.easing}`;
        card.transitionTimeoutId = setTimeout(() => {
          card.style.transition = "";
        }, tiltEffectSettings.speed);
      }
    },
    cardMouseMove: function (event) {
      if (window.matchMedia("(min-width: 600px)").matches) {
        const tiltEffectSettings = {
          max: 25, // max tilt rotation (degrees (deg))
          perspective: 500, // transform perspective, the lower the more extreme the tilt gets (pixels (px))
          scale: 1.1, // transform scale - 2 = 200%, 1.5 = 150%, etc..
          speed: 500, // speed (transition-duration) of the enter/exit transition (milliseconds (ms))
          easing: "cubic-bezier(.03,.98,.52,.99)", // easing (transition-timing-function) of the enter/exit transition
        };
        const card = event.currentTarget;
        const cardWidth = card.offsetWidth;
        const cardHeight = card.offsetHeight;
        const centerX = card.offsetLeft + cardWidth / 2;
        const centerY = card.offsetTop + cardHeight / 2;
        const mouseX = event.clientX - centerX;
        const mouseY = event.clientY - centerY;
        const rotateXUncapped =
          (+1 * tiltEffectSettings.max * mouseY) / (cardHeight / 2);
        const rotateYUncapped =
          (-1 * tiltEffectSettings.max * mouseX) / (cardWidth / 2);
        const rotateX =
          rotateXUncapped < -tiltEffectSettings.max
            ? -tiltEffectSettings.max
            : rotateXUncapped > tiltEffectSettings.max
            ? tiltEffectSettings.max
            : rotateXUncapped;
        const rotateY =
          rotateYUncapped < -tiltEffectSettings.max
            ? -tiltEffectSettings.max
            : rotateYUncapped > tiltEffectSettings.max
            ? tiltEffectSettings.max
            : rotateYUncapped;
        card.style.transform = `perspective(${tiltEffectSettings.perspective}px) rotateX(${rotateX}deg) rotateY(${rotateY}deg)
                          scale3d(${tiltEffectSettings.scale}, ${tiltEffectSettings.scale}, ${tiltEffectSettings.scale})`;
      }
    },
    cardMouseLeave: function (event) {
      if (window.matchMedia("(min-width: 600px)").matches) {
        const tiltEffectSettings = {
          max: 25, // max tilt rotation (degrees (deg))
          perspective: 500, // transform perspective, the lower the more extreme the tilt gets (pixels (px))
          scale: 1.1, // transform scale - 2 = 200%, 1.5 = 150%, etc..
          speed: 500, // speed (transition-duration) of the enter/exit transition (milliseconds (ms))
          easing: "cubic-bezier(.03,.98,.52,.99)", // easing (transition-timing-function) of the enter/exit transition
        };
        event.currentTarget.style.transform = `perspective(${tiltEffectSettings.perspective}px) rotateX(0deg) rotateY(0deg) scale3d(1, 1, 1)`;
        const card = event.currentTarget;
        clearTimeout(card.transitionTimeoutId);
        card.style.transition = `transform ${tiltEffectSettings.speed}ms ${tiltEffectSettings.easing}`;
        card.transitionTimeoutId = setTimeout(() => {
          card.style.transition = "";
        }, tiltEffectSettings.speed);
      }
    },
